import Menu from "../../containers/Menu";
import ServiceCard from "../../components/ServiceCard";
import EventCard from "../../components/EventCard";
import PeopleCard from "../../components/PeopleCard";

import "./style.scss";
import EventList from "../../containers/Events";
import Slider from "../../containers/Slider";
import Logo from "../../components/Logo";
import Icon from "../../components/Icon";
import Form from "../../containers/Form";
import Modal from "../../containers/Modal";
import { useData } from "../../contexts/DataContext";

const Page = () => {
  const { last } = useData();
  return (
    <>
      <header>
        <Menu />
      </header>
      <main>
        <section className="SliderContainer">
          <Slider />
        </section>
        <section className="ServicesContainer">
          <h2 className="Title">Nos services</h2>
          <p>Nous organisons des vnements sur mesure partout dans le monde</p>
          <div className="ListContainer">
            <ServiceCard imageSrc="/images/priscilla-du-preez-Q7wGvnbuwj0-unsplash1.png">
              <h3>Soire dentreprise</h3>
              Une soire dentreprise vous permet de runir vos quipes pour un
              moment convivial afin de valoriser votre socit en projetant une
              image dynamique. Nous vous proposons dorganiser pour vous vos
              diners et soire dentreprise
            </ServiceCard>
            <ServiceCard imageSrc="/images/hall-expo.png">
              <h3>Confrences</h3>
              724 events vous propose dorganiser votre vnement, quelle que
              soit sa taille, en sadaptant  votre demande et  vos demandes.
              En tant que spcialistes de lvnementiel, nous saurons trouver
              le lieu parfait ainsi que des solutions indites pour capter votre
              audience et faire de cet vnement un succs
            </ServiceCard>
            <ServiceCard imageSrc="/images/sophia-sideri-LFXMtUuAKK8-unsplash1.png">
              <h3>Experience digitale</h3>
              Notre agence experte en contenus immersifs offre des services de
              conseil aux entreprises, pour lutilisation de la ralit
              virtuelle, de la ralit augmente et de la ralit mixte de
              lanimation vnementielle,  la veille technologique jusquau
              dveloppement de module de formation innovant
            </ServiceCard>
          </div>
        </section>
        <section className="EventsContainer">
          <h2 className="Title">Nos ralisations</h2>
          <EventList />
        </section>
        <section className="PeoplesContainer">
          <h2 className="Title">Notre quipe</h2>
          <p>Une quipe dexperts ddis  logranisation de vos vnements</p>
          <div className="ListContainer">
            <PeopleCard
              imageSrc="/images/stephanie-liverani-Zz5LQe-VSMY-unsplash.png"
              name="Samira"
              position="CEO"
            />
            <PeopleCard
              imageSrc="/images/linkedin-sales-solutions-pAtA8xe_iVM-unsplash.png"
              name="Jean-baptiste"
              position="Directeur marketing"
            />
            <PeopleCard
              imageSrc="/images/christina-wocintechchat-com-SJvDxw0azqw-unsplash.png"
              name="Alice"
              position="CXO"
            />
            <PeopleCard
              imageSrc="/images/jonas-kakaroto-KIPqvvTOC1s-unsplash.png"
              name="Lus"
              position="Animateur"
            />
            <PeopleCard
              imageSrc="/images/amy-hirschi-b3AYk8HKCl0-unsplash1.png"
              name="Christine"
              position="VP animation"
            />
            <PeopleCard
              imageSrc="/images/christina-wocintechchat-com-0Zx1bDv5BNY-unsplash.png"
              name="Isabelle"
              position="VP communication"
            />
          </div>
        </section>
        <div
          className="FormContainer"
          id="contact"
        >
          <h2 className="Title">Contact</h2>
          <Modal
            Content={
              <div className="ModalMessage--success">
                <div>Message envoy !</div>
                <p>
                  Merci pour votre message nous tcherons de vous rpondre dans
                  les plus brefs dlais
                </p>
              </div>
            }
          >
            {({ setIsOpened }) => (
              <Form
                onSuccess={() => setIsOpened(true)}
                onError={() => null}
              />
            )}
          </Modal>
        </div>
      </main>
      <footer className="row">
        <div className="col presta">
          <h3>Notre dernire prestation</h3>
          <EventCard
            key={last?.id}
            imageSrc={last?.cover || "default-image-src"}
            title={last?.title || "Titre par dfaut"}
            date={new Date(last?.date)}
            small
            label={last?.type || "default label"}
          />
        </div>
        <div className="col contact">
          <h3>Contactez-nous</h3>
          <address>45 avenue de la Rpublique, 75000 Paris</address>
          <div>01 23 45 67 89</div>
          <div>contact@724events.com</div>
          <div>
            <a href="#twitch">
              <Icon name="twitch" />
            </a>
            <a href="#facebook">
              <Icon name="facebook" />
            </a>
            <a href="#twitter">
              <Icon name="twitter" />
            </a>
            <a href="#youtube">
              <Icon name="youtube" />
            </a>
          </div>
        </div>
        <div className="col description">
          <Logo size="large" />
          <p>
            Une agence vnementielle propose des prestations de service
            spcialises dans la conception et l&apos;organisation de divers
            vnements tels que des vnements festifs, des manifestations
            sportives et culturelles, des vnements professionnels
          </p>
        </div>
      </footer>
    </>
  );
};

export default Page;
