import { useState } from "react";
import EventCard from "../../components/EventCard";
import Select from "../../components/Select";
import { useData } from "../../contexts/DataContext";
import Modal from "../Modal";
import ModalEvent from "../ModalEvent";

import "./style.css";

const PER_PAGE = 9;

const EventList = () => {
  const { data, error } = useData();
  const [type, setType] = useState();
  const [currentPage, setCurrentPage] = useState(1);

  //  Le code vrifiait simplement l'index pour la pagination mais ne filtrait pas par type.

  // Filtrer les vnements par type slectionn
  const filteredByType =
    data?.events.filter((event) => !type || event.type === type) || [];

  // Pagination des vnements filtrs
  const filteredEvents = filteredByType.slice(
    (currentPage - 1) * PER_PAGE,
    currentPage * PER_PAGE
  );

  const changeType = (evtType) => {
    setCurrentPage(1);
    setType(evtType);
  };
  // const pageNumber = Math.floor((filteredEvents?.length || 0) / PER_PAGE) + 1;
  // Le nombre de pages tait mal calcul, ce qui  donne un rsultat incorrect.

  const pageNumber = Math.floor(filteredByType.length / PER_PAGE);
  // On s'assure que le nombre de pages est
  // calcul correctement en fonction du nombre total d'vnements
  //  filtrs (filteredByType), diviss par le nombre d'vnements par page (PER_PAGE).

  // const typeList = new Set(data?.events.map((event) => event.type));
  //  typeList tait une Set, ce qui est correct pour obtenir une liste unique de types d'vnements, mais
  //  cela devait tre converti en tableau (Array) pour tre utilis correctement dans le composant Select.

  const typeList = Array.from(new Set(data?.events.map((event) => event.type)));
  //  J'ai envelopp new Set() dans Array.from() pour convertir cet ensemble en tableau.

  return (
    <>
      {error && <div>An error occurred</div>}
      {data === null ? (
        "loading"
      ) : (
        <>
          <h3 className="SelectTitle">Catgories</h3>
          <Select
            selection={typeList}
            onChange={(value) => (value ? changeType(value) : changeType(null))}
          />
          <div
            id="events"
            className="ListContainer"
          >
            {filteredEvents.map((event) => (
              <Modal
                key={event.id}
                Content={<ModalEvent event={event} />}
              >
                {({ setIsOpened }) => (
                  <EventCard
                    onClick={() => setIsOpened(true)}
                    imageSrc={event.cover}
                    title={event.title}
                    date={event.date ? new Date(event.date) : null}
                    label={event.type}
                  />
                )}
              </Modal>
            ))}
          </div>
          <div className="Pagination">
            {[...Array(pageNumber || 0)].map((_, n) => (
              <a
                key={`page-${n + 1}`} // Utilisation d'une cl base sur le nombre de la page
                href="#events"
                onClick={() => setCurrentPage(n + 1)}
              >
                {n + 1}
              </a>
            ))}
          </div>
        </>
      )}
    </>
  );
};

export default EventList;
